.JoinPage {
    border: 4px solid #201c1c;
    background-color: #201c1c;
    height: 98.7vh;
    width: 99.4vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.joinContainer {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.joinContainer img {
    width: 10vmax;
}

.joinContainer h1 {
    font-size: 2.5rem;
    color: white;
    border-bottom: 4px solid white;
    width: 30vmax;
    text-align: center;
    padding-bottom: 10px;
}

.joinContainer input[type='text'] {
    width: 30vmax;
    height: 45px;
    outline: none;
    font-size: 20px;
    border-radius: 10px;
    padding-left: 8px;
}

.joinContainer button {
    margin-top: 25px;
    width: 30.7vmax;
    height: 45px;
    font-size: 25px;
    font-weight: 700;
    background-color: #de0f3f;
    border: none;
    color: white;
    border-radius: 10px;
    cursor: pointer;
}