.messageBox {
    font-size: 18px;
    background-color: gainsboro;
    font-weight: 400;
    padding: 1vmax;
    width: 45% ;
    border-radius: .5vmax;
    display: inline-block;
    margin: 1vmax;
    clear: both;
}
@media (max-width: 620px) { 
    .messageBox{
        font-size: 16px;
    }
}
.right{
    float: right;
    margin-bottom: -1px;
    background-color: rgb(193, 34, 66);
    color: white;
}
.left{
    float: left;
    margin-bottom: -1px;
    background-color: rgb(109, 235, 109);
    
}
.bolder{
    font-weight: 700;
}
