.chatPage {
    border: 4px solid #201c1c;
    background-color: #201c1c;
    height: 98.7vh;
    width: 99.4vw;
    display: flex;
    justify-content: center;
    align-items: center;
  
}

.chatContainer {
    background-color: white;
    height: 30rem;
    width: 50%;
}

.header {
    background-color: crimson;
    height: 15%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.header h2{
    color: white;
    margin: 1.5vmax;
}

.header a img{
    margin-right: 18px;
    transition: all .3s;
    height: 15px;
    width: 15px;
}
.header a img:hover{
    transform: scale(2);
    cursor: pointer;
}

.chatBox {
    height: 70%;
    box-sizing: border-box;
    padding-bottom: 10px;

}

.inputBox {
    height: 15%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    border: 3px solid black;

}

#chatInput {
    width: 79%;
    box-sizing: border-box;
    border: none;
    outline: none;
    font-size: 20px;
    font-weight: 700;
}

.sendBtn {
    width: 20%;
    background-color: crimson;
    color: white;
    font-size: 20px;
    font-weight: 700;
    border: none;
    cursor: pointer;
    height: 95%;
    margin-top: 1.5px;
    transition: all .3s ease-in-out;
}

.sendBtn:hover {
    background-color: rgb(197, 2, 41);
}

@media (max-width: 620px) {
    .chatContainer{
        height: 85%;
        width: 80%;
        margin-right: 1.5rem;
        
    }
    .chatBox {
        height: 75%;
    
    }
    
    .inputBox {
        height: 10%;  
    }
    }